body{
  margin:0;
  margin-left:40%;
  padding:0;
  color:rgb(209, 55, 55);
  background:rgba(47, 54, 64,1.0);
}

.topside{
  position: relative;
  margin:30%;
  max-width:100vh;
  height:auto;
  display: flex;
  align-content: center;
  align-items: center;
  flex-flow: column wrap;
}
.topside #myform {
  background: rgba(0, 151, 230,.1);
  border:1px white dashed;
  border-radius:25px;
  padding:5px 5px 5px 5px;
}
.topside #myform input{
  border:1px solid rgba(0, 151, 230,.5);
  border-radius: 50px;
  padding:5px 5px 5px 15px;
  color:white;
  background:rgba(0, 151, 230,.7);
  outline: none;
  transition: .2s;
}
.topside #myform button{
  outline: none;
  cursor: pointer;
  border:1px solid rgba(0, 151, 230,1.0);
  border-radius: 50px;
  color: white;
  background: transparent;
  padding:5px 10px 5px 10px;
  margin-left:5px;
  transition: .2s;
}
.topside #myform button:hover{
  border-radius:10px;
  background-color: rgba(0, 151, 230,.5);
  font-size:15px;
}
.topside #myform input:focus{
  border-radius:10px;
  border-color:rgb(0, 255, 0);
}

.topside .title{
  margin-bottom:5%;
  padding:10px 10px 10px 10px;
  font-size:1.2rem;
  width:0 auto;
  max-width:100vh;
  height:auto;
  color:rgba(252, 66, 123,1.0);
  font-weight: bold;

}

.itemside{
  position: relative;
  max-width:100vh;
  display:flex;
  margin-top:0;
  flex-flow: column no-wrap;
  padding:5px 5px;
  background:rgba(39, 60, 117,.18);
  border:1px dashed white;
  border-top:none;
  
}
