body{
    max-width:100vh;
    margin:0;
}

.display input{
    outline: none;
    border:1px solid transparent;
    border-radius: 80px;
    padding:15px 10px 10px 10px;
    font-size:.65rem;
    text-align: left;
    font-weight: bold;
    color:rgba(245, 246, 250,1.0);
    background-color: rgba(68, 189, 50,1.0);
}

.display span{
    cursor: pointer;
    font-size:20px;
    transition: .2s;
}

.display span:hover{
    cursor: pointer;
    font-size:23px;
    color:red;
}